        desktop: "@media only screen " + "and (min-height: ".concat(desktopOrTabletMinHeight, "px) ") + "and (min-width: ".concat(desktopMinWidth, "px) ") + "and (max-width: ".concat(largeDesktopMinWidth - 1, "px)"),
