var fromHexToRgb = function(hexColor) {
    if (hexColor.length === 4) {
        return [
            parseInt(hexColor.substr(-3, 1).repeat(2), 16),
            parseInt(hexColor.substr(-2, 1).repeat(2), 16),
            parseInt(hexColor.substr(-1, 1).repeat(2), 16), 
        ];
    }
    if (hexColor.length === 7) {
        return [
            parseInt(hexColor.substr(-6, 2), 16),
            parseInt(hexColor.substr(-4, 2), 16),
            parseInt(hexColor.substr(-2, 2), 16), 
        ];
    }
    throw Error("Bad color");
};
export var applyAlpha = function(color, alpha) {
    try {
        if (color.startsWith("var(")) {
            // it's a css variable
            return "rgba(".concat(color.slice(0, -1), "_RAW, ").concat(alpha, ")");
        } else {
            // it's a hex color
            return "rgba(".concat(fromHexToRgb(color).join(","), ", ").concat(alpha, ")");
        }
    } catch (e) {
        return color;
    }
};
